<template>
  <div class="container mt-4">
    <div class="card shadow">
      <div class="card-header bg-primary text-white">
        <h4 class="mb-0">ËæìÂÖ•Êï∞ÊçÆ</h4>
      </div>
      <div class="card-body">
        <form>
          <!-- ÈÄâÊã©ÊñπÂêë -->
          <div class="mb-3">
            <label class="form-label">ÊñπÂêë</label>
            <select class="form-select" v-model="selectedDirection" @change="updateBagua">
              <option disabled value="">ËØ∑ÈÄâÊã©ÊñπÂêë</option>
              <option v-for="direct in directions" :key="direct" :value="direct">
                {{ direct }}
              </option>
            </select>
          </div>

          <!-- ÈÄâÊã©ÂÖ´Âç¶ -->
          <div class="mb-3">
            <label class="form-label">ÂÖ´Âç¶</label>
            <select class="form-select" v-model="selectedBagua" @change="updateStems" :disabled="!selectedDirection">
              <option disabled value="">ËØ∑ÈÄâÊã©ÂÖ´Âç¶</option>
              <option v-for="bagua in baguaOptions" :key="bagua" :value="bagua">
                {{ bagua }}
              </option>
            </select>
          </div>

          <!-- ÈÄâÊã©Â§©Âπ≤Âú∞ÊîØ -->
          <div class="mb-3">
            <label class="form-label">Â§©Âπ≤Âú∞ÊîØ</label>
            <select class="form-select" v-model="selectedStems" :disabled="!selectedBagua">
              <option disabled value="">ËØ∑ÈÄâÊã©Â§©Âπ≤Âú∞ÊîØ</option>
              <option v-for="stems in stemsOptions" :key="stems" :value="stems">
                {{ stems }}
              </option>
            </select>
          </div>

          <!-- ÊåâÈíÆ -->
          <div class="d-grid">
            <button class="btn btn-success" @click.prevent="saveConfig" :disabled="!selectedStems">
              Save
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import { DIRECTIONS_BAGUA_MAPPER, BAGUA_STEMS_MAPPER } from "@/data/slotMachineData";

export default {
  name: "InputPage",
  data() {
    return {
      directions: Object.keys(DIRECTIONS_BAGUA_MAPPER),
      selectedDirection: "",
      selectedBagua: "",
      selectedStems: "",
      baguaOptions: [],
      stemsOptions: []
    };
  },
  methods: {
    updateBagua() {
      this.selectedBagua = "";
      this.selectedStems = "";
      this.baguaOptions = DIRECTIONS_BAGUA_MAPPER[this.selectedDirection] || [];
    },
    updateStems() {
      this.selectedStems = "";
      this.stemsOptions = BAGUA_STEMS_MAPPER[this.selectedBagua] || [];
    },
    async saveConfig() {
      try {
        const response = await fetch("https://safe-river-06105-d768b9a417c0.herokuapp.com/update-config", {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({
            direction: this.selectedDirection,
            bagua: this.selectedBagua,
            stems: this.selectedStems
          })
        });

        const result = await response.json();
        if (result.success) {
          alert("‚úÖ Êï∞ÊçÆÂ∑≤Â≠òÂÖ• PostgreSQLÔºÅ");
        } else {
          alert("‚ùå Êï∞ÊçÆ‰øùÂ≠òÂ§±Ë¥•Ôºö" + result.error);
        }
      } catch (error) {
        console.error("‰øùÂ≠òÂ§±Ë¥•", error);
        alert("‚ùå ËØ∑Ê±ÇÂ§±Ë¥•ÔºåËØ∑Ê£ÄÊü•ÊúçÂä°Âô®ÔºÅ");
      }
    }
  }
};
</script>

